	module.exports = {"data":{"nodePost":{"title":"Coyote Defense League Pale Ale","link":null,"body":{"value":"<p>First brew in collaboration with one of my neighbors. It came out pretty well. We got the water ratio wrong so it was a little thin, but the color and general flavor profile was pretty solid.</p>\r\n<p>Most importantly, we came up with a bunch of elaborate names. We're calling our pretend brewery 'Antcolony Brewing' - a stretch, but this is a combination of two of our kids names - Anthony and Colin (get it?) The beer itself is called 'Coyote Defense League Pale Ale' due to the surprising Coyote problem we've had in the neighborhood this year.</p>\r\n<p>Looking forward to another brew soon. Looking like something dark next time around.</p>"},"relationships":{"image":{"localFile":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":0.75,"src":"/static/02bf366e30f2109414c6479968f41c8f-7b4969a8fff234546240e67cf2ba667f-42807.jpg","srcSet":"/static/02bf366e30f2109414c6479968f41c8f-7b4969a8fff234546240e67cf2ba667f-960f9.jpg 313w,\n/static/02bf366e30f2109414c6479968f41c8f-7b4969a8fff234546240e67cf2ba667f-c36f2.jpg 625w,\n/static/02bf366e30f2109414c6479968f41c8f-7b4969a8fff234546240e67cf2ba667f-42807.jpg 1250w,\n/static/02bf366e30f2109414c6479968f41c8f-7b4969a8fff234546240e67cf2ba667f-9f2a8.jpg 1500w","sizes":"(max-width: 1250px) 100vw, 1250px"}}}}},"created":1539705836}},"pathContext":{"slug":"/note/coyote-defense-league-pale-ale/"}}

